<template>
<div class="body">

  <div>
    <div class="ban-wrap">
      <div class="banner">
        <img class="res_img" src="../assets/images/logo.605b544.jpg" alt="">
      </div>
    </div>
  </div>
  <div class="search-wrap">
    <div class="search">
      <input type="search" v-model="searchString" class="search-value" placeholder="输入展商/展位/类型/展馆">
      <button type="button" name="button" class="btn btn-info btn-circle" style="line-height: 10px;"><i class="icon iconfont icon-search"></i></button>
    </div>
  </div>
  <div class="wrap">

    <a href="javascript:;" class="main media-box_appmsg" v-for="item in filteredArticles" :key="item.index" @click="showDetail(item.exZwei),$preview.open(item.index, imgUrl)">
      <div class="weui-media-box__hd">
        <!-- 展馆、展位 -->
        <p>{{item.exZguan}}</p>
        <p class="exZwei">{{item.exZwei}}</p>
      </div>
      <div class="weui-media-box__bd">
        <!-- 展商名称 -->
        <h4 class="weui-media-box__title">{{item.exTitle}}</h4>
        <!-- 类型 -->
        <h5 class="weui-media-box__number">{{item.exType}}</h5>
      </div>
      <span class="icon iconfont icon-gengduo fa-icon"></span>
    </a>
    <div class="infoNull" v-show="searchTips">
      <h4 style="color:#000;">没有搜索到相关数据</h4>
    </div>
  </div>
  <img :src="imgUrl.src" alt="" class="media-img imgs preview-img" width="100%">
</div>
</template>
<script>
export default {
  components: {

  },
  data() {

    return {
      imgUrl: [{
        src: '',
        msrc: '',
        w: 1000,
        h: 700
      }],
      searchTips: false,
      exInfo: [],
      searchString: '',
      scroll: ''
    }
  },
  created() {
    this.getInfo();
  },
  mounted() {},
  methods: {
    getInfo() {
      this.$http.get("../static/dataExInfo.json")
        .then(res => {
          this.exInfo = res.data;
        }, res => {
          console.log("加载失败");
        })
    },
    showDetail(item) {
      let _this = this;
      _this.imgUrl["0"].src = "/static/images/" + item + ".jpg";
      _this.imgUrl["0"].msrc = "/static/images/" + item + ".jpg";
    }
  },
  computed: {
    filteredArticles: function() {
      var search_array = this.exInfo,
        searchString = this.searchString;
      this.searchTips = false;
      if (!searchString) {
        return search_array.slice(0, 4);
      }
      searchString = searchString.trim().toString();
      search_array = search_array.filter(function(item) {
        if (item.exTitle.indexOf(searchString) !== -1) {
          return item;
        }

        if (item.exZwei.toUpperCase().indexOf(searchString) !== -1) {
          return item;
        }
        if (item.exType.indexOf(searchString) !== -1) {
          return item;
        }
        if (item.exZguan.indexOf(searchString) !== -1) {
          return item;
        }

      })
      if (search_array == "") {
        this.searchTips = true;
      } else {
        this.searchTips = false;
      }
      return search_array;

    }
  }
}
</script>
<style scoped>
@import '../assets/css/iconfont.css';
@import '../assets/css/style.css';
</style>



